import { Component, Prop, Event, EventEmitter, h } from '@stencil/core';

@Component({
  tag: 'radio-button',
  styleUrl: 'radio-button.css',
  shadow: true,
})
export class RadioButton {
  /**
   * The name of the radio group.
   */
  @Prop() name!: string;

  /**
   * The value of the radio button.
   */
  @Prop() value!: string;

  /**
   * The label to display next to the radio button.
   */
  @Prop() label!: string;

  /**
   * Whether the radio button is currently checked.
   */
  @Prop({ mutable: true, reflect: true }) checked: boolean = false;

  /**
   * Whether the radio button is disabled.
   */
  @Prop() disabled: boolean = false;

  /**
   * Event emitted when the radio button's checked state changes.
   */
  @Event() myChange: EventEmitter<{ value: string; checked: boolean }> | undefined;

  private handleChange = (event: Event) => {
    if (this.disabled) {
      return;
    }

    const input = event.target as HTMLInputElement;
    this.checked = input.checked;
    this.myChange?.emit({ value: this.value, checked: this.checked });
  };

  render() {
    return (
      <label class={{ 'radio-button-wrapper': true, 'radio-button-wrapper--disabled': this.disabled }}>
        <input
          type="radio"
          name={this.name}
          value={this.value}
          checked={this.checked}
          disabled={this.disabled}
          onChange={this.handleChange}
          class="radio-button-input"
        />
        <span class="radio-button-custom"></span>
        <span class="radio-button-label">{this.label}</span>
      </label>
    );
  }
}
